import axios from "axios"
import {
    VIEW_FOOT,
    VIEW_NAV,
    UPDATE_TOTAL,
    UPDATE_HOME,
    UPDATE_LIST,
    UPDATE_DEATIL,
    VIEW_LOADING,
    UPDATE_USER,
    UPDATE_CART,
    UPDATE_LOCAL
    
}  from "./types"
export default {
    [VIEW_NAV] : ({state,commit},payload) => commit(VIEW_NAV,payload),

    [VIEW_FOOT] :  ({state,commit},payload)=> commit(VIEW_FOOT,payload),

    [UPDATE_HOME] : async ({state,commit},payload)=> {
        let res = await axios({url:"/api/home",params:{_limit :7}})
        commit(UPDATE_HOME,res.data.data)
    },

    [UPDATE_TOTAL] : async ({state,commit},payload)=> {
        let res = await axios({url:"/api/total",params:{_limit :6}})
        commit(UPDATE_TOTAL,res.data.data)
    },

    [UPDATE_LIST] : async ({state,commit},payload)=> {
        let res = await axios({url:"/api/list",params:{_limit :18}})
        commit(UPDATE_LIST,res.data.data)
    },

    // [UPDATE_DEATIL] : async ({state,commit},payload)=> {
    //     console.log(payload)
    //     let res = await axios({url:"/api/Detail/"+payload})
    //     console.log(res.data.data)
    //     //commit(UPDATE_DEATIL,res.data.data)
    // },
    [UPDATE_DEATIL] :  ({state,commit},payload)=> {
        axios({
            url:"/api/list/"+payload,
        }).then(
            //res =>console.log(res.data.data)
            res=>commit(UPDATE_DEATIL,res.data.data)
        );
    },

    [VIEW_LOADING] :  ({state,commit},payload)=> commit(VIEW_LOADING,payload),

    [UPDATE_USER] :  ({state,commit},{username,password})=> {
        return axios({
            url:'/api/login',
            method:'post',
            data:{username,password,save:false}
        }).then(
            //res => console.log(res.data)
            res=>{
                commit(UPDATE_USER,res.data)//->state
                localStorage.setItem('jinlong_user',JSON.stringify(res.data))//同步一份locastorage
                return res.data
            }
        )
    },
    [UPDATE_CART] :  ({state,commit},payload)=> {
        axios({
            url:'/api/cart',
            method:'get',
            params:{user_id:payload,_limit :1800}
        }).then(
            //res => console.log(res.data)
            res=>{
                console.log(res)
                commit(UPDATE_CART,res.data)//->state
            }
        )
    }

}