<template>
  <div id="app">
    <div v-show="loading" class="loading"><img src="/loading.gif" alt=""></div>
    <Header v-show="bNav"></Header>
    <router-view></router-view>
    <Foot v-show="bFoot"></Foot>
  </div>
</template>

<script>
//头部
import Header from "./components/Header"
import Foot from "./components/Foot"
import {mapGetters} from "vuex"
import * as types from "./plugins/store/types"
export default {
  name: 'app',
  components: {
    Header,Foot
  },
  computed: mapGetters([
    "loading","bNav","bFoot"
  ]),
  watch:{
    $route:{//路由监听-》数据观测
      handler(to){
        let path = to.path;
        console.log(path)

        if(/Home/.test(path)){

          this.$store.dispatch(types.VIEW_NAV,true)
          this.$store.dispatch(types.VIEW_FOOT,true)
        }

        if(/List|Cart|login|Reg|User|Detail/.test(path)){
          this.$store.dispatch(types.VIEW_NAV,true);
          this.$store.dispatch(types.VIEW_FOOT,false)
        }
       
      },
      immediate:true
    }
  },
}
</script>

<style scoped>
.loading{
  position: fixed;width:32px;height:32px;left:0;right:0;top:0;bottom:0;margin:auto;z-index: 999;
}
.loading img{width:100%}
</style>
