let routes =[
    {path:"/Home",name:"home",component:r => { require(['../pages/Home'], r) }},
    {path:"/User",name:"user",component:r => { require(['../pages/User'], r) }},
    {path:"/Login",name:"login",component:r => { require(['../pages/Login'], r) }},
    {path:"/Reg",name:"reg",component:r => { require(['../pages/Reg'], r) }},
    {path:"/List",name:"list",component:r => { require(['../pages/List'], r) }},
    {path:"/Detail/:id",name:"detail",component:r => { require(['../pages/Detail'], r) }},
    {path:"/Cart",name:"cart",component:r => { require(['../pages/Cart'], r) }},
    {path:"/",redirect: "/Home"}
]

let router = new VueRouter({
    routes,
    //滚动行为
    scrollBehavior (to, from, savedPosition) {
        return { x: 0, y: 0 }
    }
})

export default router